<template>
  <span class="sdf-ui-icon" @click="onClick" :style="{ fontSize: size +'px'}">
    <svg v-if="symbol" class="sdf-ui-icon__symbol" aria-hidden="true">
      <use :xlink:href="`#vc-icon-${name}`"></use>
    </svg>
    <i v-else class="sdf-ui-icon__font" :class="'vc-icon-' + name" :style="{ color: color }"></i>
  </span>
</template>
<script>
import './iconfont.js'
import create from '../utils/create-basic'
export default create({
  name: 'icon',
  props: {
    // symbol
    symbol: {
      type: Boolean,
      default: true
    },
    // 
    name: {
      type: String,
      required: true
    },
    // 
    size: {
      type: Number
    },
    // symbol
    color: {
      type: String
    }
  },
  computed: {
    fontSize () {
      const viewportWidth = 750
      const ratio = viewportWidth / 100

      return this.size / ratio
    }
  },
  methods: {
    onClick () {
      this.$emit('click')
    }
  }
})
</script>
